  body #root .toggle:checked + .hamburger > div {
    transform: rotate(135deg); }
  body #root .toggle:checked + .hamburger > div:before,
  body #root .toggle:checked + .hamburger > div:after {
    top: 0;
    transform: rotate(90deg); }
  body #root .toggle:checked:hover + .hamburger > div {
    transform: rotate(225deg); }
  body #root div.hamburger {
    z-index: 10;
    position: fixed;
